.tarea-contenedor {
  width: 450px;
  min-height: 65px; /* tiene una altura minima por si la tarea tiene mucho texto*/
  background-color: black;
  margin: 5px 0;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 7px;
  border: 1px solid white;
  cursor: pointer;
  box-shadow: 5px 5px #252525;
}

/* Colores */

.tarea-contenedor {
  background-color: steelblue;
}

/* Texto */

.tarea-texto {
  width: 100%;
  height: 100%;
  font-size: 20px;
  display: flex;
  align-items: center;
  overflow-wrap: anywhere; /* si el texto es muy largo el texto va a ser cortada y va a ir hacia abajo */
}

/* Icono */

.tarea-icono {
  width: 25px;
  height: 25px;
  margin: 5px;
}

/* Tarea completada */

.tarea-contenedor.completada {
  background-color: #4b844b;
  text-decoration: line-through;
}


